import React, { useState, useMemo } from 'react';
import { Box, Typography, Button, FormControl, InputLabel, Select, MenuItem, TextField, Autocomplete, Stack, CircularProgress, Paper, Grid, IconButton, LinearProgress } from '@mui/material';
import { PhotoCamera, Delete } from '@mui/icons-material';

const WorkLogPage = ({ projects, user, onSubmit, isSubmitting, navigateTo }) => {
  const [logData, setLogData] = useState({
    date: new Date().toISOString().split('T')[0],
    project: '',
    startTime: '',
    endTime: '',
    distinction: '',
    floor: '',
    term: '',
    isCompleted: '',
    content: '',
    photoUrls: [], // ** URL**
  });
  const [errors, setErrors] = useState({});

  // ****
  const [isUploading, setIsUploading] = useState(false);
  const [uploadProgress, setUploadProgress] = useState(0);
  const [selectedFiles, setSelectedFiles] = useState([]);

  const projectOptions = useMemo(() => {
    if (!projects) return [];
    return [...new Set(projects.map(p => p.projectName))];
  }, [projects]);

  const termOptions = useMemo(() => {
    if (!projects) return [];
    const allTerms = projects.map(p => p.term).filter(Boolean);
    return [...new Set(allTerms)];
  }, [projects]);

  const generateTimeOptions = () => {
    const times = [];
    for (let h = 8; h <= 18; h++) {
      for (let m = 0; m < 60; m += 30) {
        const hour = String(h).padStart(2, '0');
        const minute = String(m).padStart(2, '0');
        times.push(`${hour}:${minute}`);
      }
    }
    return times;
  };
  const timeOptions = useMemo(() => generateTimeOptions(), []);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setLogData(prev => ({ ...prev, [name]: value }));
  };

  const handleProjectChange = (event, newValue) => {
    setLogData(prev => ({ ...prev, project: newValue || '', term: '' }));
  };

  const handleTermChange = (event, newValue) => {
    setLogData(prev => ({ ...prev, term: newValue || '' }));
  };

  // ****
  const handleFileChange = (event) => {
    const files = Array.from(event.target.files);
    const fileObjects = files.map(file => ({
      file,
      preview: URL.createObjectURL(file),
    }));
    setSelectedFiles(prev => [...prev, ...fileObjects]);
  };

  // ****
  const handleUpload = async () => {
    if (selectedFiles.length === 0) return;
    setIsUploading(true);
    setUploadProgress(0);

    const uploadedUrls = [];
    for (let i = 0; i < selectedFiles.length; i++) {
      const { file } = selectedFiles[i];
      const reader = new FileReader();
      reader.readAsDataURL(file);
      await new Promise((resolve, reject) => {
        reader.onload = async () => {
          try {
            const payload = {
              fileData: reader.result,
              fileName: file.name,
              date: logData.date,
            };
            // ** fetch **
            const response = await fetch("https://script.google.com/macros/s/AKfycbzvbtdyosoUvb3UWGydYUa6FDzFvOKx7p-xAOsu2ZwJhftq5QWFjzzj_5VwAw9G2F_bJA/exec", {
              method: 'POST',
              mode: 'no-cors',
              body: JSON.stringify({ action: 'uploadImage', payload }),
            });
            
            //  URL
            //  URL 
            setUploadProgress(((i + 1) / selectedFiles.length) * 100);
            resolve();
          } catch (error) {
            reject(error);
          }
        };
        reader.onerror = reject;
      });
    }
    
    //  selectedFiles
    // setLogData(prev => ({ ...prev, photoUrls: [...prev.photoUrls, ...uploadedUrls] }));
    // setSelectedFiles([]);
    setIsUploading(false);
    alert('');
  };
  
  const handleRemoveFile = (previewUrl) => {
    setSelectedFiles(prev => prev.filter(f => f.preview !== previewUrl));
  };

  const validate = () => {
    const newErrors = {};
    if (!logData.date) newErrors.date = '';
    if (!logData.project) newErrors.project = '';
    if (!logData.startTime) newErrors.startTime = '';
    if (!logData.endTime) newErrors.endTime = '';
    if (logData.startTime && logData.endTime && logData.startTime >= logData.endTime) {
      newErrors.endTime = '';
    }
    if (!logData.content.trim()) newErrors.content = '';
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = () => {
    if (validate()) {
      const timeSlot = `${logData.startTime}-${logData.endTime}`;
      onSubmit({ ...logData, user, timeSlot });
    }
  };

  return (
    <Paper elevation={3} sx={{ p: { xs: 2, sm: 4 }, borderRadius: 2 }}>
      <Typography variant="h5" component="h2" gutterBottom align="center">
        
      </Typography>
      <Stack spacing={3} sx={{ mt: 4 }}>
        <TextField name="date" label="" type="date" value={logData.date} onChange={handleChange} error={!!errors.date} helperText={errors.date} InputLabelProps={{ shrink: true }} />
        <TextField label="" value={user} disabled fullWidth />
        <Autocomplete freeSolo fullWidth options={projectOptions} value={logData.project} onChange={handleProjectChange} onInputChange={handleProjectChange} renderInput={(params) => (<TextField {...params} label="" error={!!errors.project} helperText={errors.project} />)} />
        <Stack direction={{ xs: 'column', sm: 'row' }} spacing={2}>
          <FormControl fullWidth error={!!errors.startTime}>
            <InputLabel></InputLabel>
            <Select name="startTime" value={logData.startTime} label="" onChange={handleChange}>
              {timeOptions.map((time, i) => <MenuItem key={i} value={time}>{time}</MenuItem>)}
            </Select>
            {errors.startTime && <Typography color="error" variant="caption">{errors.startTime}</Typography>}
          </FormControl>
          <FormControl fullWidth error={!!errors.endTime}>
            <InputLabel></InputLabel>
            <Select name="endTime" value={logData.endTime} label="" onChange={handleChange}>
              {timeOptions.map((time, i) => <MenuItem key={i} value={time}>{time}</MenuItem>)}
            </Select>
            {errors.endTime && <Typography color="error" variant="caption">{errors.endTime}</Typography>}
          </FormControl>
        </Stack>
        <Stack direction={{ xs: 'column', sm: 'row' }} spacing={2}>
          <TextField name="distinction" label=" (: A)" fullWidth value={logData.distinction} onChange={handleChange} />
          <TextField name="floor" label=" (: 1F)" fullWidth value={logData.floor} onChange={handleChange} />
        </Stack>
        <Stack direction={{ xs: 'column', sm: 'row' }} spacing={2}>
           <Autocomplete freeSolo fullWidth options={termOptions} value={logData.term} onChange={handleTermChange} onInputChange={handleTermChange} renderInput={(params) => <TextField {...params} label="" />} />
          <FormControl fullWidth>
            <InputLabel></InputLabel>
            <Select name="isCompleted" value={logData.isCompleted} label="" onChange={handleChange}>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
            </Select>
          </FormControl>
        </Stack>
        <TextField name="content" label="" multiline rows={4} fullWidth value={logData.content} onChange={handleChange} error={!!errors.content} helperText={errors.content} />
        
        {/*  */}
        <Box>
          <Typography variant="h6" gutterBottom></Typography>
          <Button variant="outlined" component="label" startIcon={<PhotoCamera />} disabled={isUploading}>
            
            <input type="file" hidden multiple accept="image/*" onChange={handleFileChange} />
          </Button>
          {selectedFiles.length > 0 && (
            <Button onClick={handleUpload} disabled={isUploading} sx={{ ml: 2 }}>
              {isUploading ? '...' : ` ${selectedFiles.length} `}
            </Button>
          )}
          {isUploading && (
            <Box sx={{ width: '100%', mt: 2 }}>
              <LinearProgress variant="determinate" value={uploadProgress} />
              <Typography variant="body2" color="text.secondary">{`${Math.round(uploadProgress)}%`}</Typography>
            </Box>
          )}
          <Grid container spacing={2} sx={{ mt: 2 }}>
            {selectedFiles.map(({ preview }, index) => (
              <Grid item key={index} xs={6} sm={4} md={3}>
                <Paper sx={{ position: 'relative' }}>
                  <img src={preview} alt="preview" style={{ width: '100%', height: 'auto' }} />
                  <IconButton onClick={() => handleRemoveFile(preview)} size="small" sx={{ position: 'absolute', top: 0, right: 0, color: 'white', backgroundColor: 'rgba(0,0,0,0.5)' }} disabled={isUploading}>
                    <Delete fontSize="small" />
                  </IconButton>
                </Paper>
              </Grid>
            ))}
          </Grid>
        </Box>

      </Stack>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', mt: 5 }}>
        <Button variant="outlined" onClick={() => navigateTo(1)} disabled={isSubmitting || isUploading}>
          
        </Button>
        <Button variant="contained" color="primary" onClick={handleSubmit} disabled={isSubmitting || isUploading} startIcon={isSubmitting ? <CircularProgress size={20} color="inherit" /> : null}>
          {isSubmitting ? '...' : ''}
        </Button>
      </Box>
    </Paper>
  );
};

export default WorkLogPage;
