import React, { useState, useEffect, useMemo } from 'react';
import { Box, Typography, Select, MenuItem, Button, FormControl, InputLabel, TextField, Grid, Stack, Autocomplete } from '@mui/material';

const ProjectInfoPage = ({ projects, formData, updateFormData, navigateTo, allUsers }) => {
  const [data, setData] = useState({
    project: formData.project || '',
    deliveryAddress: formData.deliveryAddress || '',
    deliveryDate: formData.deliveryDate || '',
    recipientName: formData.recipientName || '',
    recipientPhone: formData.recipientPhone || '',
  });
  const [errors, setErrors] = useState({});

  // ****
  useEffect(() => {
    if (data.recipientName) {
      const foundUser = allUsers.find(u => u.recipient === data.recipientName);
      if (foundUser && foundUser.recipientPhone) {
        setData(prev => ({ ...prev, recipientPhone: foundUser.recipientPhone }));
      } else {
        setData(prev => ({ ...prev, recipientPhone: '' })); // ****
      }
    }
  }, [data.recipientName, allUsers]);


  const handleChange = (e) => {
    const { name, value } = e.target;
    setData(prev => ({ ...prev, [name]: value }));
  };
  
  const handleRecipientChange = (event, newValue) => {
    setData(prev => ({ ...prev, recipientName: newValue || '' }));
  };

  const validate = () => {
    const newErrors = {};
    if (!data.project) newErrors.project = '';
    if (!data.deliveryAddress) newErrors.deliveryAddress = '';
    if (!data.deliveryDate) newErrors.deliveryDate = '';
    if (!formData.userPhone) newErrors.userPhone = '';
    if (!data.recipientName) newErrors.recipientName = '';
    if (!data.recipientPhone) newErrors.recipientPhone = '';
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleNext = () => {
    if (validate()) {
      //  data  formData  userPhone 
      updateFormData({ ...data, userPhone: formData.userPhone });
      navigateTo(3);
    }
  };
  
  const recipientOptions = useMemo(() => {
    if (!allUsers) return [];
    const names = allUsers.map(u => u.recipient).filter(Boolean);
    return [...new Set(names)];
  }, [allUsers]);

  return (
    <Box>
      <Typography variant="h5" component="h2" gutterBottom align="center">
        
      </Typography>
      <Stack spacing={3} sx={{ mt: 4 }}>
        <FormControl fullWidth error={!!errors.project}>
          <InputLabel></InputLabel>
          <Select name="project" value={data.project} label="" onChange={handleChange}>
            {projects.map((p, i) => <MenuItem key={i} value={p}>{p}</MenuItem>)}
          </Select>
        </FormControl>
        
        <TextField name="deliveryAddress" label="" fullWidth value={data.deliveryAddress} onChange={handleChange} error={!!errors.deliveryAddress} helperText={errors.deliveryAddress} />
        
        <TextField name="deliveryDate" label="" type="date" fullWidth value={data.deliveryDate} onChange={handleChange} error={!!errors.deliveryDate} helperText={errors.deliveryDate} InputLabelProps={{ shrink: true }} />

        <Grid container spacing={2}>
            <Grid item xs={12} sm={12}>
                <TextField label="" type="tel" fullWidth value={formData.userPhone} disabled error={!!errors.userPhone} helperText={errors.userPhone} />
            </Grid>
             <Grid item xs={12} sm={6} className="recipient-name-grid">
                <Autocomplete
                  freeSolo
                  options={recipientOptions}
                  value={data.recipientName}
                  onChange={handleRecipientChange}
                  onInputChange={handleRecipientChange}
                  renderInput={(params) => (
                    <TextField 
                      {...params} 
                      label="" 
                      error={!!errors.recipientName} 
                      helperText={errors.recipientName} 
                    />
                  )}
                />
            </Grid>
            <Grid item xs={12} sm={6} className="recipient-phone-grid">
                <TextField name="recipientPhone" label="" type="tel" fullWidth value={data.recipientPhone} onChange={handleChange} error={!!errors.recipientPhone} helperText={errors.recipientPhone} />
            </Grid>
        </Grid>
      </Stack>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', mt: 5 }}>
        <Button variant="outlined" onClick={() => navigateTo(1)}></Button>
        <Button variant="contained" onClick={handleNext}></Button>
      </Box>
    </Box>
  );
};

export default ProjectInfoPage;