import React from 'react';
import { Box, Typography, Button, List, ListItem, ListItemText, Divider, Grid, CircularProgress, Paper } from '@mui/material';

const InfoItem = ({ primary, secondary }) => (
  <Grid item xs={12} sm={6}>
    <Paper variant="outlined" sx={{ p: 2, height: '100%' }}>
      <Typography variant="subtitle2" color="text.secondary">{primary}</Typography>
      <Typography variant="body1" fontWeight="500">{secondary || ''}</Typography>
    </Paper>
  </Grid>
);

const PreviewPage = ({ formData, navigateTo, onSubmit, isSubmitting }) => {
  const { user, project, deliveryAddress, deliveryDate, userPhone, recipientName, recipientPhone, cart } = formData;

  return (
    <Box>
      <Typography variant="h5" component="h2" gutterBottom align="center"></Typography>
      
      <Grid container spacing={2} sx={{ mt: 3 }}>
        <InfoItem primary="" secondary={user} />
        <InfoItem primary="" secondary={project} />
        <InfoItem primary="" secondary={deliveryAddress} />
        <InfoItem primary="" secondary={deliveryDate} />
        <InfoItem primary="" secondary={userPhone} />
        <InfoItem primary="" secondary={recipientName} />
        <InfoItem primary="" secondary={recipientPhone} />
      </Grid>

      <Typography variant="h6" sx={{ mt: 4, mb: 2 }}></Typography>
      <Paper variant="outlined" sx={{ maxHeight: '30vh', overflowY: 'auto' }}>
        <List dense>
          {cart.length > 0 ? cart.map((item, index) => (
            <React.Fragment key={item.id}>
              <ListItem>
                <ListItemText
                  primary={`${item.subcategory} - ${item.size}`}
                  secondary={`: ${item.category}, : ${item.thickness}`}
                />
                <Typography variant="body1" fontWeight="500">{item.quantity} {item.unit}</Typography>
              </ListItem>
              {index < cart.length - 1 && <Divider />}
            </React.Fragment>
          )) : (
            <ListItem>
              <ListItemText primary="" sx={{ textAlign: 'center', color: 'text.secondary' }} />
            </ListItem>
          )}
        </List>
      </Paper>

      <Box sx={{ display: 'flex', justifyContent: 'space-between', mt: 5 }}>
        <Button variant="outlined" onClick={() => navigateTo(3)} disabled={isSubmitting}>
          
        </Button>
        <Button
          variant="contained"
          color="success"
          size="large"
          onClick={onSubmit}
          disabled={isSubmitting || cart.length === 0}
          startIcon={isSubmitting ? <CircularProgress size={20} color="inherit" /> : null}
        >
          {isSubmitting ? '...' : ''}
        </Button>
      </Box>
    </Box>
  );
};

export default PreviewPage;
